html,
body {
  height: 100%;
}
.app-wrapper {
  max-width: 1900px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas:
    "h h"
    "n c ";
}

.main-content {
  grid-area: c;
  background-color: darksalmon;
  min-height: 500px;
}
